import React, { useState } from "react";
import { TareasRow } from "./components/TareasRow";
import { Banner } from "./components/Banner";
import { TareasCreator } from "./components/TareasCreator";

function App() {
  const [taskItems, setTaskItems] = useState([
    { name: "Tarea Ejemplo 1", done: false },
    { name: "Tarea Ejemplo 2", done: false },
  ]);

  const nuevaTarea = (taskName) => {
    if (!taskItems.find((t) => t.name === taskName)) {
      setTaskItems([...taskItems, { name: taskName, done: false }]);
    } else {
      console.log("tarea repetida");
    }
  };

  const deleteTask = (task) =>
    setTaskItems(
      taskItems.map((t) => (t.name === task.name ? { ...t, done: !t.done } : t))
    );

  const editTask = (task, editedTask) =>
    setTaskItems(
      taskItems.map((t) => (t.name === task ? { ...t, name: editedTask } : t))
    );

  const tareasTableRows = (done) =>
    taskItems
      .filter((task) => task.done === done)
      .map((task) => (
        <TareasRow
          task={task}
          key={task.name}
          deleteTask={deleteTask}
          editTask={editTask}
        />
      ));

  return (
    <div className="row justify-content-center">
      <Banner taskItems={taskItems} />
      <div className="col-4 mt-5">
        <h4>Nueva Tarea:</h4>
        <TareasCreator callback={nuevaTarea} />
      </div>
      <div className="col-6 mt-5">
        <h4>Tareas Pendientes:</h4>
        <table className="table table-stripped border">
          <thead>
            <tr>
              <th>
                <p>Tarea</p>
              </th>
              <th>
                <p>Realizada</p>
              </th>
            </tr>
          </thead>
          <tbody>{tareasTableRows(false)}</tbody>
        </table>
      </div>
    </div>
  );
}
export default App;
